/* -------------------------------------------------------------------------- */
/*                                CSS Variables                               */
/* -------------------------------------------------------------------------- */
:root {
  /* --------------------------------- Colors --------------------------------- */
  --nfd-editor-chat-color-primary: #2271b1;
  --nfd-editor-chat-color-primary-hover: #135e96;
  --nfd-editor-chat-color-primary-active: #0e4b72;
  --nfd-editor-chat-color-primary-light-8: rgba(34, 113, 177, 0.08);
  --nfd-editor-chat-color-primary-light-12: rgba(34, 113, 177, 0.12);
  --nfd-editor-chat-color-primary-light-16: rgba(34, 113, 177, 0.16);
  --nfd-editor-chat-color-primary-light-20: rgba(34, 113, 177, 0.2);
  --nfd-editor-chat-color-grey-dark: #666;
  --nfd-editor-chat-color-grey-medium: #999;
  --nfd-editor-chat-color-grey-light: #ddd;
  --nfd-editor-chat-color-grey-placeholder: #9e9e9e;
  --nfd-editor-chat-color-border: #e0e0e0;
  --nfd-editor-chat-color-background: #f5f5f5;
  --nfd-editor-chat-color-text: #1e1e1e;
  --nfd-editor-chat-color-white: #fff;
  /* -------------------------------- Typography -------------------------------- */
  --nfd-editor-chat-font-size-base: 14px;
}

.components-button[aria-controls*=nfd-editor-chat].is-active, .components-button[aria-controls*=nfd-editor-chat]:hover, .components-button[aria-controls*=nfd-editor-chat]:focus, .components-button[aria-controls*=nfd-editor-chat].is-pressed {
  background: transparent !important;
  color: inherit !important;
}

.nfd-editor-chat-ai-avatar {
  border-radius: 50%;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
}
.nfd-editor-chat-ai-avatar svg {
  fill: currentColor;
}

.nfd-editor-chat-suggestions {
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
  width: 100%;
  padding: 0 0 16px 0;
  justify-content: center;
}

.nfd-editor-chat-suggestion {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 8px;
  padding: 8px 12px;
  background: var(--nfd-editor-chat-color-white);
  border: 1px solid var(--nfd-editor-chat-color-border);
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.15s ease;
  text-align: left;
  white-space: nowrap;
  flex-shrink: 0;
  color: var(--nfd-editor-chat-color-grey-dark);
}
.nfd-editor-chat-suggestion.components-button svg {
  fill: none;
}
.nfd-editor-chat-suggestion.components-button:hover {
  background: var(--nfd-editor-chat-color-background);
  color: var(--nfd-editor-chat-color-text);
}
.nfd-editor-chat-suggestion.components-button:active {
  background: var(--nfd-editor-chat-color-grey-light);
  color: var(--nfd-editor-chat-color-text);
}
.nfd-editor-chat-suggestion.components-button:focus {
  outline: none;
  background: var(--nfd-editor-chat-color-background);
  border-color: var(--nfd-editor-chat-color-primary);
  color: var(--nfd-editor-chat-color-text);
}

.nfd-editor-chat-suggestion__icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 16px;
  height: 16px;
  flex-shrink: 0;
  stroke: var(--nfd-editor-chat-color-grey-dark);
}
.nfd-editor-chat-suggestion__icon svg {
  width: 14px;
  height: 14px;
  fill: none;
}

.nfd-editor-chat-suggestion__text {
  font-size: var(--nfd-editor-chat-font-size-base);
  line-height: 1.3;
  font-weight: 400;
  text-align: left;
}

.interface-complementary-area__fill:has(#nfd-editor-chat),
#nfd-editor-chat {
  width: 400px !important;
}

#nfd-editor-chat {
  display: flex;
  flex-direction: column;
  max-height: 100%;
  overflow: hidden;
}

.nfd-editor-chat-sidebar__content {
  display: flex;
  flex-direction: column;
  height: 100%;
  background: #fff;
  padding: 0;
  position: relative;
  flex-grow: 1;
}

.nfd-editor-chat-sidebar__panel {
  overflow-y: auto;
}

.interface-complementary-area.nfd-editor-chat-sidebar {
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.interface-complementary-area .nfd-editor-chat-sidebar__header {
  position: relative;
}

.components-panel.nfd-editor-chat-sidebar__panel {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.nfd-editor-chat-sidebar__header {
  padding: 16px;
  border-bottom: 1px solid #e0e0e0;
  position: relative;
}
.nfd-editor-chat-sidebar__header h2 {
  margin: 0;
  font-size: var(--nfd-editor-chat-font-size-base);
  font-weight: 600;
}

.nfd-editor-chat-sidebar__header-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.nfd-editor-chat-sidebar__header-actions {
  display: flex;
  gap: 4px;
  align-items: center;
}

.nfd-editor-chat-sidebar__new-chat.components-button,
.nfd-editor-chat-sidebar__expand.components-button {
  min-width: auto;
  width: 32px;
  height: 32px;
  padding: 0;
  stroke: var(--nfd-editor-chat-color-grey-dark);
  background: transparent;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.15s ease;
}
.nfd-editor-chat-sidebar__new-chat.components-button:hover:not(:disabled),
.nfd-editor-chat-sidebar__expand.components-button:hover:not(:disabled) {
  stroke: var(--nfd-editor-chat-color-primary);
  background: var(--nfd-editor-chat-color-primary-light-12);
}
.nfd-editor-chat-sidebar__new-chat.components-button:focus:not(:disabled),
.nfd-editor-chat-sidebar__expand.components-button:focus:not(:disabled) {
  outline: none;
  stroke: var(--nfd-editor-chat-color-primary);
  background: var(--nfd-editor-chat-color-primary-light-16);
}
.nfd-editor-chat-sidebar__new-chat.components-button:active:not(:disabled),
.nfd-editor-chat-sidebar__expand.components-button:active:not(:disabled) {
  stroke: var(--nfd-editor-chat-color-primary-hover);
  background: var(--nfd-editor-chat-color-primary-light-20);
}
.nfd-editor-chat-sidebar__new-chat.components-button svg,
.nfd-editor-chat-sidebar__expand.components-button svg {
  display: block;
  fill: none;
  width: 16px;
  height: 16px;
}

.nfd-editor-chat-input {
  padding: 16px;
  background: var(--nfd-editor-chat-color-white);
  z-index: 10;
  border-top: 1px solid var(--nfd-editor-chat-color-border);
}

.nfd-editor-chat-input__disclaimer {
  text-align: center;
  font-size: 12px;
  color: var(--nfd-editor-chat-color-grey-medium);
  margin-top: 8px;
  line-height: 1.4;
}
.nfd-editor-chat-input__disclaimer a {
  color: var(--nfd-editor-chat-color-grey-dark);
  text-decoration: none;
  font-weight: 500;
}
.nfd-editor-chat-input__disclaimer a:hover {
  text-decoration: underline;
}

.nfd-editor-chat-input__container {
  display: flex;
  flex-direction: column;
  gap: 8px;
  background: var(--nfd-editor-chat-color-white);
  border-radius: 12px;
  padding: 12px;
  border: 1px solid var(--nfd-editor-chat-color-border);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  transition: all 0.2s ease;
}
.nfd-editor-chat-input__container:focus-within {
  box-shadow: 0 2px 12px rgba(34, 113, 177, 0.25);
}

.nfd-editor-chat-input__textarea {
  width: 100%;
  border: none;
  background: transparent;
  resize: none;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
  font-size: var(--nfd-editor-chat-font-size-base);
  line-height: 1.5;
  padding: 4px;
  color: var(--nfd-editor-chat-color-text);
  min-height: 24px;
  max-height: 200px;
  overflow-y: auto;
}
.nfd-editor-chat-input__textarea:focus {
  outline: none;
  border: none;
  box-shadow: none;
}
.nfd-editor-chat-input__textarea::placeholder {
  color: var(--nfd-editor-chat-color-grey-placeholder);
}
.nfd-editor-chat-input__textarea:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  box-shadow: none;
}
.nfd-editor-chat-input__textarea::-webkit-scrollbar {
  width: 8px;
}
.nfd-editor-chat-input__textarea::-webkit-scrollbar-track {
  background: transparent;
}
.nfd-editor-chat-input__textarea::-webkit-scrollbar-thumb {
  background: var(--nfd-editor-chat-color-border);
  opacity: 0.8;
  border-radius: 4px;
}
.nfd-editor-chat-input__textarea::-webkit-scrollbar-thumb:hover {
  opacity: 1;
}

.nfd-editor-chat-input__actions {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 4px;
}

.components-button.nfd-editor-chat-input__submit {
  flex-shrink: 0;
  min-width: auto;
  width: 28px;
  height: 28px;
  padding: 0;
  stroke: var(--nfd-editor-chat-color-grey-dark);
  background: var(--nfd-editor-chat-color-background);
  border: none;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.15s ease;
}
.components-button.nfd-editor-chat-input__submit:hover:not(:disabled) {
  stroke: var(--nfd-editor-chat-color-primary);
  background: var(--nfd-editor-chat-color-primary-light-12);
}
.components-button.nfd-editor-chat-input__submit:focus:not(:disabled) {
  outline: none;
  stroke: var(--nfd-editor-chat-color-primary);
  background: var(--nfd-editor-chat-color-primary-light-16);
}
.components-button.nfd-editor-chat-input__submit:active:not(:disabled) {
  stroke: var(--nfd-editor-chat-color-primary-hover);
  background: var(--nfd-editor-chat-color-primary-light-20);
}
.components-button.nfd-editor-chat-input__submit:disabled {
  opacity: 0.4;
  stroke: var(--nfd-editor-chat-color-grey-medium);
  background: var(--nfd-editor-chat-color-background);
  cursor: not-allowed;
}
.components-button.nfd-editor-chat-input__submit svg {
  display: block;
  fill: none;
  width: 16px;
  height: 16px;
}

.components-button.nfd-editor-chat-input__attach {
  flex-shrink: 0;
  min-width: auto;
  width: 28px;
  height: 28px;
  padding: 0;
  stroke: var(--nfd-editor-chat-color-text);
  background: var(--nfd-editor-chat-color-background);
  border: none;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.15s ease;
}
.components-button.nfd-editor-chat-input__attach:hover:not(:disabled) {
  stroke: var(--nfd-editor-chat-color-primary);
  background: var(--nfd-editor-chat-color-primary-light-12);
}
.components-button.nfd-editor-chat-input__attach:focus:not(:disabled) {
  outline: none;
  stroke: var(--nfd-editor-chat-color-primary);
  background: var(--nfd-editor-chat-color-primary-light-16);
}
.components-button.nfd-editor-chat-input__attach:active:not(:disabled) {
  stroke: var(--nfd-editor-chat-color-primary-hover);
  background: var(--nfd-editor-chat-color-primary-light-20);
}
.components-button.nfd-editor-chat-input__attach:disabled {
  opacity: 0.4;
  stroke: var(--nfd-editor-chat-color-grey-medium);
  background: var(--nfd-editor-chat-color-background);
  cursor: not-allowed;
}
.components-button.nfd-editor-chat-input__attach svg {
  display: block;
  fill: none;
  width: 16px;
  height: 16px;
}

.nfd-editor-chat-messages {
  display: flex;
  flex: 1 1 auto;
  flex-direction: column;
  overflow-y: auto;
  padding: 20px 24px;
  gap: 20px;
  min-height: 0;
}

.nfd-editor-chat-message {
  display: flex;
  gap: 12px;
  align-items: flex-start;
  font-size: var(--nfd-editor-chat-font-size-base);
}
.nfd-editor-chat-message--assistant {
  flex-direction: row;
  justify-content: flex-start;
}
.nfd-editor-chat-message--assistant .nfd-editor-chat-message__content {
  flex: 1;
  line-height: 1.6;
  color: #1e1e1e;
  padding-top: 4px;
  background: none;
  padding: 0;
  border-radius: 0;
  max-width: none;
}
.nfd-editor-chat-message--user {
  justify-content: flex-end;
}
.nfd-editor-chat-message--user .nfd-editor-chat-message__avatar {
  flex-shrink: 0;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background: #e0e0e0;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #424242;
  font-weight: 600;
  overflow: hidden;
}
.nfd-editor-chat-message--user .nfd-editor-chat-message__avatar .nfd-editor-chat-message__avatar-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
}
.nfd-editor-chat-message--user .nfd-editor-chat-message__content {
  background: #f5f5f5;
  padding: 12px 16px;
  border-radius: 16px;
  line-height: 1.5;
  color: #1e1e1e;
  max-width: 70%;
}

.nfd-editor-chat-messages::-webkit-scrollbar {
  width: 8px;
}
.nfd-editor-chat-messages::-webkit-scrollbar-track {
  background: transparent;
}
.nfd-editor-chat-messages::-webkit-scrollbar-thumb {
  background: var(--nfd-editor-chat-color-border);
  opacity: 0.8;
  border-radius: 4px;
}
.nfd-editor-chat-messages::-webkit-scrollbar-thumb:hover {
  opacity: 1;
}

.nfd-editor-chat-welcome {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 40px 16px 0 16px;
  text-align: center;
  min-height: 0;
  overflow-y: auto;
  gap: 32px;
}

.nfd-editor-chat-welcome__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex: 1;
}

.nfd-editor-chat-welcome__avatar {
  margin-bottom: 24px;
}

.nfd-editor-chat-welcome__message {
  max-width: 320px;
  margin-bottom: 0;
}

.nfd-editor-chat-welcome__title {
  font-size: 18px;
  line-height: 1.4;
  color: var(--nfd-editor-chat-color-text);
  font-weight: 600;
  margin-bottom: 8px;
}

.nfd-editor-chat-welcome__subtitle {
  font-size: var(--nfd-editor-chat-font-size-base);
  line-height: 1.5;
  color: var(--nfd-editor-chat-color-grey-dark);
  font-weight: 400;
}

.nfd-editor-chat-typing-indicator {
  display: flex;
  align-items: center;
  gap: 8px;
  padding-top: 4px;
}

.nfd-editor-chat-typing-indicator__message {
  font-size: var(--nfd-editor-chat-font-size-base);
  color: var(--nfd-editor-chat-color-grey-medium);
  font-style: italic;
}

.nfd-editor-chat-typing-indicator__loader {
  width: 20px;
  aspect-ratio: 2;
  --_g: no-repeat
  	radial-gradient(circle closest-side, var(--nfd-editor-chat-color-grey-medium) 90%, transparent);
  background: var(--_g) 0% 50%, var(--_g) 50% 50%, var(--_g) 100% 50%;
  background-size: 33.3333333333% 40%;
  animation: nfd-editor-chat-loader 1s infinite linear;
}

@keyframes nfd-editor-chat-loader {
  20% {
    background-position: 0% 100%, 50% 50%, 100% 50%;
  }
  40% {
    background-position: 0% 0%, 50% 100%, 100% 50%;
  }
  60% {
    background-position: 0% 50%, 50% 0%, 100% 100%;
  }
  80% {
    background-position: 0% 50%, 50% 50%, 100% 0%;
  }
}